export default {
    props: {
        bannerData: {
            type: Array,
            default: function(e) {
                return [];
            }
        },
        height: { // 高宽百分比
            type: Number,
            default: function(e) {
                return 100;
            }
        }
    },
    methods: {
        _click: function(index) {
            this.$emit('click', index);
        },
    }
};
